export type Language = "en" | "id" | "es" | "pt" | "de" | "fr" | "ru" | "zh" | "ja" | "ko" | "tr" | "ar"

export function t(key: string, lang: Language = "en"): string {
  return translations[lang]?.[key] || translations["en"]?.[key] || key
}

export function getLanguageName(code: Language): string {
  const names: Record<Language, string> = {
    en: "English",
    id: "Bahasa Indonesia",
    es: "Espaol",
    pt: "Portugus",
    de: "Deutsch",
    fr: "Franais",
    ru: "",
    zh: "",
    ja: "",
    ko: "",
    tr: "Trke",
    ar: "",
  }
  return names[code] || code
}

export function getLanguageFlag(code: Language): string {
  const flags: Record<Language, string> = {
    en: "",
    id: "",
    es: "",
    pt: "",
    de: "",
    fr: "",
    ru: "",
    zh: "",
    ja: "",
    ko: "",
    tr: "",
    ar: "",
  }
  return flags[code] || ""
}

export function getAllLanguages(): Language[] {
  return ["en", "id", "es", "pt", "de", "fr", "ru", "zh", "ja", "ko", "tr", "ar"]
}
